          :key="m.id"
        >{{m}}</span>
        {{$store.state.commodity_data.title}}
      </p>
      <p class="price" v-if="$route.params.status == 1">
        <span class="orc" v-if="flag">
          ￥
          <em>{{context}}</em>
        </span>
        <span class="orc" v-if="flags">
          会员价￥
          <em>{{$store.state.commodity_data.price_member}}</em>
        </span>
        <span class="new">
          市场价￥
          <s>{{$store.state.commodity_data.price_market}}</s>
        </span>
      </p>
      <div v-if="postersShow" class="share" @click="shareHref()">
        <i class="icon"></i>
        <p>分享</p>
      </div>
      <div v-else class="share" @click="sharePoster()">
        <i class="icon"></i>
        <p>分享海报</p>
      </div>

      <!--<p class="timer clearfix" v-show="Number($store.state.commodity_data.minStock) <= 10">库存：{{$store.state.commodity_data.minStock}}</p>-->
    </div>
    <div class="describe" v-if="$store.state.commodity_data.goods">
      <p class="fn">{{$store.state.commodity_data.title || $store.state.commodity_data.goods.title}}</p>
      <!--<p class="timer">促销剩余时间:365天12小时50分钟23秒</p>-->
      <p class="price" v-if="$route.params.status == 1">
        <span class="orc">
          ￥
          <em>{{$store.state.commodity_data.price_member}}</em>
        </span>
        <span class="new">
          原价 ：￥
          <s>{{$store.state.commodity_data.price_market}}</s>
        </span>
      </p>
      <div class="price active" v-if="$store.state.commodity_data.goods">
        <p class="orc clearfix">
          <em class="icon fl"></em>
          <em class="fl">{{$store.state.commodity_data.goods.integral}}</em>
        </p>
        <p class="new">
          市场参考价：
          <s>{{$store.state.commodity_data.goods.price_market}}</s>元
        </p>
      </div>
      <div class="share">
        <i class="icon"></i>
        <p>分享</p>
      </div>
    </div>
    <!-- <div class="selected" @click="theSon">已选<span class="number">数量&nbsp;{{$store.state.commodity_val}}</span><span class="btn-right"></span></div> -->
    <div class="detail_coupons" @click="openLayer" v-if="couponDate.length !=0">
      <span class="tit">领劵</span>
      <span class="num fr">共{{couponDate.length || 0}}张</span>
      <div class="coupon_ske" v-for="(item,index) in couponDate" :key="index">
        <span v-if="item.reduced_type == 1" class="coupon">满{{item.condition}}减{{item.money}}</span>
        <span v-else class="coupon">满{{item.condition}} {{item.discount*0.1}}折</span>
      </div>
      <span class="btn-right"></span>
    </div>
    <div class="gui clearfix specifications" v-for="(item,index) in spec_title" :key="item.id">
      <p>{{item.name}}</p>
      <div class="roll" ref="boxHeight">
        <div class="roll_content">
          <span
            :data-id="data.id"
            :data-spec-id="data.spec_id"
            v-if="item.id == data.spec_id"
            v-for="(data,i) in guigedata.spec.spec_children"
            :key="data.id"
            @click="addClass(index,$event)"
            :class="{active: guigedata.goods[specKey].key.indexOf(data.id)>-1}"
          >{{data.item}}</span>
        </div>
      </div>
    </div>
    <div class="addSub clearfix">
      <span class="pull-left fl">数量</span>
      <div class="pull-right fr clearfix">
        <span
          class="stock fl"
          v-if="$store.state.commodity_data.minStock == 0"
        >库存：{{parseInt($store.state.commodity_data.minStock)}}件</span>
        <div class="input-main fl clearfix">
          <button class="fl" @click="reduce">-</button>
          <input
            v-if="$store.state.commodity_data"
            type="tel"
            class="fl"
            :maxlength="$store.state.commodity_data.minStock"
            v-model="$store.state.commodity_val"
            @change="min"
          />
          <button class="fl" @click="plusGoods">+</button>
        </div>
      </div>
    </div>
    <div class="picture_details">
      <div class="title">
        <span class="title fl">图文详情</span>
      </div>
      <div class="list-img-wrap" v-html="img_list"></div>
    </div>
    <div v-if="shareShow" id="dcontent" class="dcontent">
      <textarea id="sharecontent" class="sharehref" rows="3">1</textarea>
      <p class="heading">链接地址：</p>
      <input
        id="sharehref"
        class="sharehref"
        type="url"
        :value="URL + '/mobile/index.html'+ url"
        placeholder="请输入要分享的链接地址"
      />
      <p class="heading">链接标题：</p>
      <input
        id="sharehrefTitle"
        class="sharehref"
        type="text"
        :value="$store.state.commodity_data.title"
        placeholder="请输入要分享的链接标题"
      />
      <p class="heading">链接描述：</p>
      <input
        id="sharehrefDes"
        class="sharehref"
        type="text"
        :value="$store.state.commodity_data.title"
        placeholder="请输入要分享的链接描述"
      />
      <!-- <div class="button" @click="shareHref()">分享链接</div> -->
    </div>
    <!-- <div class="prompt" @click="toTab">点击查看更多商品信息</div> -->
    <!--<detail-option :text="text"></detail-option>-->
    <foot-btn :state="sonState" :data="$store.state.commodity_data"></foot-btn>
    <mt-popup @touchmove.prevent v-model="popupVisible" position="bottom">
      <div class="main">
        <div class="header">
          优惠券
          <i class="close" @click="close"></i>
        </div>
        <div class="content">
          <div class="coupon_list">
            <div class="coupon" v-for="(item,index) in couponDate" :key="index">
              <div class="coupon_main">
                <div class="price-box">
                  <p class="view_price">
                    <i v-if="item.reduced_type == 1">￥</i>
                    <strong v-if="item.reduced_type == 1">{{item.money}}</strong>
                    <strong v-else>
                      {{item.discount*0.1}}
                      <span>折</span>
                    </strong>
                  </p>
                  <p class="view_des">满{{item.condition}}元可用</p>
                </div>
                <div class="coupon_info">
                  <p class="coupon_info_text">
                    <i class="coupon_info_type">{{item.name}}</i>
                    {{item.instructons}}
                  </p>
                  <span
                    v-if="item.have_status == 1"
                    class="coupon_info_btn"
                    @click="receiveCoupon(item.id,index)"
                  >领取</span>
                  <span v-else class="coupon_info_btn disabled">已领取</span>
                  <p class="coupon_info_date" v-if="item.time_type == 1">
                    {{item.use_start_time |
                    formatDate}} - {{item.use_end_time | formatDate}}
                  </p>
                  <p class="coupon_info_date" v-else>自领取后{{item.limited_time}}天有效</p>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </mt-popup>
    <div class="load-wrap" v-show="load_wrap" @touchmove.prevent>
      <mt-spinner type="triple-bounce" color="rgb(38, 162, 255)"></mt-spinner>
    </div>
    <!-- 弹框的html -->
    <div
      id="picture"
      class="mui-popover mui-popover-action mui-popover-bottom"
      style="z-index: 99999999"
    >
      <ul class="mui-table-view">
        <li class="mui-table-view-cell">
          <a href="javascript:;" id="saveImg">保存图片</a>
        </li>
      </ul>
      <ul class="mui-table-view">
        <li class="mui-table-view-cell">
          <a href="#picture">
            <b>取消</b>
          </a>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import countDown from "../home/children/children/countDown";
import PrHeader from "./children/shop_header.vue";
import FootBtn from "./children/footBtn.vue";
import { Popup, Toast, MessageBox } from "mint-ui";
import detailOption from "./children/detailOptions.vue";
import qs from "qs";

export default {
  name: "product",
  data() {
    return {
      popupVisible: false,
      couponDate: [],
      mark: {},
      shareShow: true,
      img_list: "",
      //是否显示状态条
      showIndicators: true,
      //初始轮播切换索引
      defaultIndex: 0,
      //轮播是否循环播放
      continuous: true,
      number: 1,
      conItem: {
        title: "猜你喜欢"
      },
      scrollWatch: true,
      topStatus: "",
      loadTop: {},
      lv_switch: false,
      sonState: false,
      data: "",
      text: "",
      title: "商品详情",
      dataLeave: "",
      load_wrap: true,
      endTime: "",
      guigedata: "",
      spec_title: "",
      specKey: this.$route.params.id,
      sw: 1,
      shares: null,
      // url:window.location.hash
      url: "",
      reco_code: "",
      postersShow: false,
      secKillText: "",
      hour: "",
      Minute: "",
      second: "",
      isStart: false,
      timer: null,
      flag: true,
      flags: false,
      context: "注册会员看优惠价格"
    };
  },
  beforeRouteLeave(to, from, next) {
    MessageBox.close(false);
    $("#middlePopover").hide();
    $(".mui-backdrop.mui-active").css("opacity", "0");
    next();
  },
  watch: {
    popupVisible() {
      if (this.popupVisible === true) {
        document.body.style.overflowY = "hidden";
      } else {
        document.body.style.overflowY = "auto";
      }
    }
  },
  computed: {
    percentage() {
      let stock = parseInt(this.$store.state.commodity_data.strock);
      let total_stock = parseInt(this.$store.state.commodity_data.total_stock);
      let result = ((total_stock - stock) / total_stock) * 100;
      let str = result.toString().split(".");
      return str[0];
    }
  },
  methods: {
    //获取可领取优惠券列表
    getCouponsList() {
      this.axios
        .post(
          API_URL + "Home/Coupon/getCouponsList",
          qs.stringify({
            app_user_id: sessionStorage.getItem("user_ID"),
            goods_id: this.specKey
          })
        )
        .then(res => {
          if (res.data.status) {
            let data = res.data.data;
            for (let index in data) {
              // data[index].condition = data[index].condition.substring(0, data[index].condition.length - 3);
              // data[index].money = data[index].money.substring(0, data[index].money.length - 3);
              this.couponDate.push(data[index]);
            }
          }
        })
        .catch(err => {
          console.log(err);
        });
    },
    //领取优惠券
    receiveCoupon(id, index) {
      if (!sessionStorage.getItem("user_ID")) {
        Toast("请先登录");
        this.$router.push("/LogoIn");
        return;
      }
      this.axios
        .post(
          API_URL + "Home/Coupon/getCoupon",
          qs.stringify({
            app_user_id: sessionStorage.getItem("user_ID"),
            id: id
          })
        )
        .then(res => {
          if (res.data.status) {
            Toast({
              message: "领取成功~",
              position: "middle",
              className: "tost_z_index",
              duration: 1000
            });
            this.couponDate[index].have_status = res.data.data.have_status;
          } else {
            Toast({
              message: res.data.msg,
              position: "middle",
              className: "tost_z_index",
              duration: 1000
            });
          }
        })
        .catch(err => {
          console.log(err);
        });
    },
    getMark() {
      this.axios({
        url: API_URL + "Home/Index/getGoodsMarking",
        method: "get"
      })
        .then(res => {
          this.mark = res.data.data;
        })
        .catch(err => {
          console.log(err);
        });
    },
    /**
     * 分享操作
     * @param {JSON} sb 分享操作对象s.s为分享通道对象(plus.share.ShareService)
     * @param {Boolean} bh 是否分享链接
     */
    shareAction(sb, bh) {
      //  alert('分享操作：');
      if (!sb || !sb.s) {
        // alert('无效的分享服务！');
        return;
      }
      var msg = {
        content: sharecontent.value,
        extra: {
          scene: sb.x
        }
      };
      if (bh) {
        msg.href = sharehref.value;
        if (sharehrefTitle && sharehrefTitle.value != "") {
          msg.title = sharehrefTitle.value;
        }
        if (sharehrefDes && sharehrefDes.value != "") {
          msg.content = sharehrefDes.value;
        }
        msg.thumbs = [this.URL + this.$store.state.commodity_data.goods_img[0]];
        msg.pictures = ["_www/logo.png"];
      } else {
        if (pic && pic.realUrl) {
          msg.pictures = [pic.realUrl];
        }
      }
      // 发送分享
      if (sb.s.authenticated) {
        // alert('---已授权---');
        this.shareMessage(msg, sb.s);
      } else {
        // alert('---未授权---');
        sb.s.authorize(
          function() {
            this.shareMessage(msg, sb.s);
          },
          function(e) {
            //    alert('认证授权失败：'+e.code+' - '+e.message);
          }
        );
      }
    },
    /**
     * 发送分享消息
     * @param {JSON} msg
     * @param {plus.share.ShareService} s
     */
    shareMessage(msg, s) {
      // outLine(JSON.stringify(msg));
      s.send(
        msg,
        function() {
          Toast('分享到"' + s.description + '"成功！');
        },
        function(e) {
          Toast('分享到"' + s.description + '"失败！');
        }
      );
    },
    // H5 plus事件处理
    plusReady() {
      this.updateSerivces();
    },
    /**
     * 更新分享服务
     */
    updateSerivces() {
      var _this = this;
      if (typeof plus === "undefined") {
        this.shareShow = false;
        return false;
      }
      plus.share.getServices(
        function(s) {
          _this.shares = {};
          for (var i in s) {
            var t = s[i];
            _this.shares[t.id] = t;
          }
        },
        function(e) {
          alert("获取分享服务列表失败：" + e.message);
        }
      );
    },
    shareHref() {
      var shareBts = [];
      var _this = this;
      // 更新分享列表
      var ss = this.shares["weixin"];
      ss &&
        ss.nativeClient &&
        (shareBts.push({
          title: "微信朋友圈",
          s: ss,
          x: "WXSceneTimeline"
        }),
        shareBts.push({
          title: "微信好友",
          s: ss,
          x: "WXSceneSession"
        }));
      ss = this.shares["qq"];
      ss &&
        ss.nativeClient &&
        shareBts.push({
          title: "QQ",
          s: ss
        });
      // 弹出分享列表
      shareBts.length > 0
        ? plus.nativeUI.actionSheet(
            {
              title: "分享链接",
              cancel: "取消",
              buttons: shareBts
            },
            function(e) {
              e.index > 0 && _this.shareAction(shareBts[e.index - 1], true);
            }
          )
        : plus.nativeUI.alert("当前环境无法支持分享链接操作!");
    },
    sharePoster() {
      var user_str = sessionStorage.getItem("user_ID");
      if (!user_str) {
        Toast("请先登录");
        return;
      }
      this.axios({
        url: API_URL + "Home/Goods/getSharePicture",
        method: "get",
        params: {
          app_user_id: user_str,
          goods_id: this.$route.params.id
        }
      })
        .then(res => {
          MessageBox({
            title: "",
            showConfirmButton: false,
            cancelButtonClass: false,
            message:
              '<p id="imgsul"><img class="saveImg" style="width:4.5rem; height:6.75rem;margin-bottom:.3rem;" src=' +
              this.URL +
              res.data.data +
              "></p><p>长按保存，分享好友</p>"
          });
          this.publicMethod.savePictures();
        })
        .catch(err => {
          console.log(err);
        });
    },
    callback() {
      this.$store.state.buy_text_status = "立即抢购";
      // console.log('已经结束了！')
    },
    //样式切换
    styleTab(ele) {
      if ($(ele).hasClass("active")) {
        $(ele).removeClass("active");
      } else {
        $(ele).addClass("active");
      }
    },
    //选中
    addClass(index, event) {
      let selectedList = [],
        sortList = "",
        str = "",
        goodsId = "";
      $(".specifications span").each(function(i, e) {
        let specId = $(e).data("spec-id");
        if (specId == index) {
          $(e).removeClass("active");
        }
      });
      this.styleTab(event.currentTarget);
      //获取所有选中的规格
      $(".specifications span").each(function(i, e) {
        if ($(e).hasClass("active")) {
          selectedList.push($(e).data("id"));
        }
      });
      //排序
      sortList = selectedList.sort((a, b) => {
        return a > b ? 1 : -1;
      });
      //字符串拼接
      str = sortList.join("_");
      //对比
      for (let i in this.guigedata.goods) {
        if (str === this.guigedata.goods[i].key) {
          goodsId = this.guigedata.goods[i].id;
        }
      }
      this.$store.state.link_id = goodsId;
      this.$store.state.goods_id = goodsId;
      clearInterval(this.timer);
      this.ax();
      console.log(this.$store.state.goods_id);
    },
    //图文详情
    pictureDetails() {
      this.axios({
        url: API_URL + "Home/Goods/goodsDetail",
        method: "get",
        params: {
          goods_id: this.$route.params.id
        }
      })
        .then(res => {
          this.img_list = res.data.data.detail;
        })
        .catch(err => {
          console.log(err);
        });
    },
    openLayer() {
      this.popupVisible = true;
    },
    close() {
      this.popupVisible = false;
    },
    myLove() {
      this.axios({
        //猜你喜欢
        url: API_URL + "Home/Goods/my_love",
        method: "get",
        params: {
          app_user_id: sessionStorage.getItem("user_ID")
        }
      })
        .then(res => {
          this.$store.state.dataLeave = res.data.data;
        })
        .catch(err => {
          console.log(err);
        });
    },
    //处理倒计时
    processingTime() {
      let self = this;
      let now_Time = this.$store.state.commodity_data.now;
      let start_Time = this.$store.state.commodity_data.starttime;
      let two_day_end = this.$store.state.commodity_data.two_day_end;
      this.timer = setInterval(function() {
        let t = 0;
        let now = new Date(now_Time * 1000);
        let start = new Date(start_Time * 1000);
        let twoDayEnd = new Date(two_day_end * 1000);
        if (now > start) {
          self.isStart = true;
          self.secKillText = "距离结束还有";
          t = twoDayEnd.getTime() - now.getTime();
          self.callback();
        } else {
          self.isStart = false;
          t = start.getTime() - now.getTime();
          self.secKillText = "距离开抢还有";
        }
        if (t > 0) {
          let hour = Math.floor(t / 3600000);
          let min = Math.floor((t / 60000) % 60);
          let sec = Math.floor((t / 1000) % 60);
          hour = hour < 10 ? "0" + hour : hour;
          min = min < 10 ? "0" + min : min;
          sec = sec < 10 ? "0" + sec : sec;
          self.hour = `${hour}`;
          self.Minute = `${min}`;
          self.second = `${sec}`;
        } else {
          clearInterval(self.timer);
          self.hour = `00`;
          self.Minute = `00`;
          self.second = `00`;
        }
        now_Time += 1;
      }, 1000);
    },
    ax() {
      if (this.$route.params.status != 2) {
        this.axios({
          url: API_URL + "Home/Goods/goods",
          method: "get",
          params: {
            goods_id: this.$store.state.link_id,
            app_user_id: sessionStorage.getItem("user_ID")
          }
        })
          .then(res => {
            console.log(res);
            this.$store.state.commodity_data = res.data.data;
            this.endTime = res.data.data.starttime;
            this.$store.state.nowTime = parseInt(res.data.data.now);
            this.$store.state.catr_number = parseInt(res.data.data.goods_num);
            if (this.$store.state.commodity_data.c_type != 0) {
              this.processingTime();
            }
            // if(this.$store.state.catr_number <= 0){
            //     this.$store.state.catr_number = '0';
            // }
            this.load_wrap = false;
          })
          .catch(err => {
            console.log(err);
          });
      } else {
        this.axios
          .post(
            API_URL + "Home/Integral/integral_goodsdetail",
            qs.stringify({
              goods_id: this.$store.state.link_id
            })
          )
          .then(res => {
            console.log(res);
            this.$store.state.commodity_data = res.data.data;
            this.text = res.data.data;
            this.load_wrap = false;
          })
          .catch(err => {
            console.log(err);
          });
      }
    },
    intoHtml(str) {
      var arrEntities = { lt: "<", gt: ">", nbsp: " ", amp: "&", quot: '"' };
      return str.replace(/&(lt|gt|nbsp|amp|quot);/gi, function(all, t) {
        return arrEntities[t];
      });
    },
    reduce() {
      if (this.$store.state.commodity_val <= 1) return;
      this.$store.state.commodity_val--;
    },
    plusGoods() {
      if (
        this.$store.state.commodity_val >=
        this.$store.state.commodity_data.minStock
      )
        return;
      this.$store.state.commodity_val++;
    },
    min() {
      let val = parseInt(this.$store.state.commodity_val);
      let reg = /(^[1-9]([0-9]*)$|^[0-9]$)/;
      if (!reg.test(this.$store.state.commodity_val)) {
        this.$store.state.commodity_val = 1;
      }
      if (val >= this.$store.state.commodity_data.minStock)
        this.$store.state.commodity_val = this.$store.state.commodity_data.minStock;
    },
    getSpec() {
      this.axios
        .post(
          API_URL + "Home/Goods/goodSpecsByGoodsId",
          qs.stringify({
            goods_id: this.$store.state.link_id
          })
        )
        .then(res => {
          console.log(res);
          this.spec_title = res.data.data.spec.spec_group;
          this.guigedata = res.data.data;
        })
        .catch(err => {
          console.log(err);
        });
    }
  },
  
  created() {
    console.log(this.$route.params.num);
    sessionStorage.removeItem("goods_id");
    this.$store.state.link_id = "";
    this.$store.state.goods_id = "";
    this.$store.state.link_id = this.$route.params.id;
    this.$store.commit("clear_data", this.$store.state.commodity_data);
    this.$store.commit("buy_text", "立即抢购");
    this.getSpec();
    this.getMark();
    this.getCouponsList();
    this.axios
      .post(
        API_URL + "Home/index/gerRecoCode",
        qs.stringify({
          app_user_id: sessionStorage.getItem("user_ID")
        })
      )
      .then(res => {
        this.reco_code = res.data.data;
        this.url =
          "#/register?reco_code=" + res.data.data + "&goods_id=" + this.specKey;
      })
      .catch(err => {
        console.log(err);
      });
  },
  mounted() {
    console.log(this.specKey);
    document.body.scrollTop = 0;
    document.body.style.overflowY = "auto";
    if (window.plus) {
      this.plusReady();
    } else {
      document.addEventListener("plusready", this.plusReady(), false);
    }
    if (this.client_type == 2) {
      this.postersShow = true;
    } else {
      this.postersShow = false;
    }
    this.pictureDetails();
    this.$store.commit("value", this.$store.state.commodity_val);
    this.$store.commit("shops_switch", this.$store.state.const_join);
    if (sessionStorage.getItem("user_ID")) {
      this.lv_switch = true;
    }
    if (this.$route.params.status == 2) {
      this.axios
        .post(
          API_URL + "Home/Integral/integral_goodsdetail",
          qs.stringify({
            goods_id: this.$store.state.link_id
          })
        )
        .then(res => {
          console.log(res);
          this.$store.state.commodity_data = res.data.data;
          this.text = res.data.data;
          this.load_wrap = false;
        })
        .catch(err => {
          console.log(err);
        });
    } else {
      this.ax();
    }
    if (localStorage.getItem("user_ID")) {
      this.flags = true;
      this.flag = false;
    } else {
      this.flag = true;
      this.flags = false;
    }
  },
  destroyed() {
    this.scrollWatch = false;
  },
  components: {
    PrHeader,
    FootBtn,
    detailOption,
    countDown
  }
};
</script>

