              color: #fff;
              font-size: 0.24rem;
            }

            .disabled {
              border: 0.02rem solid #999;
              background-color: #fff !important;
              color: #999;
            }

            .coupon_info_date {
              position: absolute;
              left: 0;
              bottom: 0;
              padding-right: 1.36rem;
              box-sizing: border-box;
              width: 100%;
              line-height: 0.4rem;
              font-size: 0.2rem;
              color: #999;
              overflow: hidden;
              text-overflow: ellipsis;
              white-space: nowrap;
            }
          }
        }
      }
    }
  }
}
</style>



